.header{
    grid-area: head;
    background-color: $color-grey-light-1;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &-logo{
    margin-left: 4rem;
    height: 4.6rem;
    display: block;
 }
}

.search{
    background-color: #fff;
    border-radius: 10rem;
    display: flex;
    align-items: center;
    padding-left: 3rem;
    transition: all 0.3s;

    &:focus-within{
        transform: translateY(-2px);
        box-shadow: 0 0.7rem 3rem rgba($color-grey-dark-1, 0.08);
    }

    &-field{
        border: none;
        background: none;
        font-family: inherit;
        color: inherit;
        font-size: 1.7rem;
        width: 30rem;

        &:focus{
            outline: none;
        }
        &::placeholder{
            color: $color-grey-light-3;
        }
        @media only screen and (max-width: $bp-medium) {
            width: auto;
        
            &::placeholder{
                color: #fff;
            }
        }
    }

    &-btn{
        font-weight: 600;
        font-family: inherit;
    }
}

.nav{
    align-self: stretch;
    margin-right: 2.5rem;
 
    &-list{
        list-style: none;
        display: flex;
        height: 100%;
    }
    &-item{
        position: relative;
    }
    &-btn{
        height: 100%;
        font-family:inherit;
        color: inherit;
        font-size: 1.4rem;
        font-weight: 700;
        text-transform: uppercase;
        background:none;
        border: none;
        cursor: pointer;
        padding: 0 1.5rem;
        transition: all 0.3s;

        display: flex;
        align-items: center;

        svg{
            height: 2.4rem;
            width: 2.4rem;
            fill: $color-primary;
            margin-right: 0.7rem;
            transform: translateY(-1px);
        }
        &:focus{
            outline: none;
        }
        &:hover{
            background-color: $color-grey-light-2;
        }
    }

}

.bookmarks{
    padding: 1rem 0;
    position: absolute;
    //right: 0;
    right: -2.5rem;
    z-index: 10;
    width: 40rem;
    background-color: #fff;
    box-shadow: 0 0.8rem 5rem 2rem rgba($color-grey-dark-1, 0.1);

    visibility: hidden;
    opacity: 0;
    transition: all 0.5s 0.2s;

    &-list{
        list-style: none;
    }
    &-field{
        cursor: pointer;
        padding: 0 4rem;

        display: flex;
        align-items: center;
        height: 100%;
        transition: all 0.3s;
    
    &:hover{
        background-color: $color-grey-light-2;
    }
    }

    &:hover,
    .nav-btn-bookmark:hover + &{
        visibility: visible;
        opacity: 1;
    }
}